*{
    box-sizing:border-box;
    padding:0;
    margin:0;
}


a{
    color:white;
    text-decoration: none;
}
.navbar{
    width:100%;
    height:10vh;
    background: black;
    color:white;
    display:flex;
    align-items: center;
    justify-content: space-evenly;
}

.footer{
    width:100%;
    height:10vh;
    background: black;
    color:white;
    display:flex;
    align-items: center;
    justify-content: center;
}

.book-list{
    min-height: 80vh;
    width:100%;
    display:grid;
    grid-template-columns: repeat(3,1fr);
    justify-items:center;
    text-align: center;
    margin:20px 0;
}

.favorites{
    min-height: 80vh;
    width:100%;
    display:grid;
    grid-template-columns: repeat(3,1fr);
    justify-items:center;
    text-align: center;
    margin:20px 0;
}

img{
    width:300px;
    height:400px;
    transition:all 0.3s ease;
}

img:hover{
    transform:scale(1.1);
}

.book{
    text-align: center;
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content:center;

}

button{
    padding:10px;
    background:black;
    color:white;
    width:300px;
    font-size: 20px;
    border-radius: 5px;

}